.main {
    min-height: 93vh;
    display: flex;
    align-items: center;
    background-image: url("../../Background.jpg");
    background-position: center;
}

.title {
    font-size: 95px;
}

.subtitle {
    font-size: 25px;
    padding-top: 10px;
}

.intro-text {
    width: 100%;
    text-align: center;
}

.buttonContainer {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
}

.landingbutton {
    width: 200px;
    height: 55px;
}

@media only screen and (min-width:1200px){
    .buttonContainer {
        padding:0 200px;
    }
    .title{
        padding: 0 100px;
    }
}

@media only screen and (max-width:768px){
    .landingbutton {
       width: 150px;
       height: 50px;
    }
    .title{
        font-size: 60px;
    }
}